$bg-color: #fefffe;
$main: #cce3de;
$text-color: #26332d;

.todo-list {
  font-family: "Open sans", sans-serif;
  background-color: $bg-color;
  height: 100vh;
  width: 100vw;
  margin-top: -3rem;
  .container {
    max-width: 80rem;
    height: 100vh;
    margin: 2rem auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: $text-color;
  }

  h1 {
    font-weight: 300;
    font-size: 3.2rem;
    letter-spacing: 0.3px;
    display: inline-block;
    color: #000;
  }
  .input-container {
    width: 100%;
    padding: 1.2rem;
  }

  input {
    width: 100%;
    border: 1px solid $main;
    padding: 0.8rem 1rem;
    border-radius: 1px;
    color: $text-color;

    &:focus {
      outline: 0;
      box-shadow: 0 0 0 0.1rem $main;
    }
    &:active {
      box-shadow: 0 0 0 0.2rem $main;
    }
  }

  ul {
    font-size: 1.4rem;
    font-weight: 400;
    list-style: none;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    color: $text-color;
  }

  .wrapper {
    display: grid;
    grid-template-columns: 3fr 2fr;
    width: 100%;
    max-height: 50rem;
    margin-top: 3.2rem;
    gap: 3.2rem;
    padding: 0 1.2rem;
    overflow: auto;

    @media (max-width: 600px) {
      grid-template-columns: 1fr;
    }
  }

  .todo {
    width: 100%;
    border: 1px solid $main;
    border-radius: 2px;
    background-color: #fff;

    &__header {
      font-size: 1.8rem;
      font-weight: 500;
      border-bottom: 1px solid $main;
      padding: 1rem;
      background-color: #ffee93;
      color: $text-color;
    }

    &__list {
      color: $text-color;
    }
  }

  .list-container {
    display: flex;
    border-bottom: 1px solid #cce3de;
    justify-content: space-between;
    padding: 1.6rem 1rem;
    gap: 0.4rem;
  }
  .btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
  }

  .btn {
    outline: none;
    width: 3.6rem;
    height: 3.2rem;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
    box-sizing: initial;
    padding: 0;
    &--1 {
      background-color: #93ecb5;
      border: 1px solid #32e275;

      &:hover {
        background-color: #32e275;
      }
    }

    &--2 {
      background-color: #ffadad;
      border: 1px solid #ff4e4e;
      width: 3.2rem;

      &:hover {
        background-color: #ff4e4e;
      }
    }
  }

  svg {
    user-select: none;
    pointer-events: none;
  }

  //COMPLETED
  .completed {
    width: 100%;
    background: #fff;
    border: 1px solid $main;

    &__header {
      font-size: 1.8rem;
      font-weight: 500;
      border-bottom: 1px solid $main;
      padding: 1rem;
      background-color: #80ed99;
      color: $text-color;
    }

    &__list {
      color: $text-color;
    }
  }

  .completed-container {
    padding: 1.6rem 1rem;
    border-bottom: 1px solid $main;
    display: grid;
    grid-template-columns: repeat(1, 1fr 16px);
    align-items: center;
    justify-content: center;
    gap: 1rem;

    & svg {
      justify-self: center;
    }
  }
}
